    gap: 20px;
}

.product__card .stock__item button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;
    border: none;
    background: none;
    color: cornflowerblue;
    font-size: 25px;
}

.product__card .stock__item .stock__btnrst{
    position: relative;
    left: 10px;
}
.product__card .stock__item .stock__btnadd{
    position: relative;
    right: 10px;
}
.product__card .stock__item p{
    padding: 0 30px;
    margin: 5px;
}
.cart__itemWidget{
    width: 100%;
    display: flex;
    background-color: white;
}

.cart__itemWidget .itemWidget__data{
    display: flex;
