}

.nav__cart .cart__widget{
    display: none;
}

.nav__cart .active{
    display: block;
    background-color: rgb(204, 199, 186);
    z-index: 1000;
    position:absolute;
    width: 40vh;
    height: 100vh;
    top: 0;
    right: 0;
}

.nav__cart .active .cart__widget__title{
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav__cart .active .cart__widget__title h4{
