.home__product__card .home__product__card__img img{
    z-index: -100;
    left: 0;
    width: 100%;
    height: 210px;
}

.home__product__card button{
    width: 100%;
    height: 40px;
    margin-top: 15px;
    border-radius: 0;
    border-style: none;
    border: 1px black solid;
    font-weight: bold;
    font-size: 15px;
    font-family: 'latoblack';
    text-transform: uppercase;
}